body {
  font-size: 18px;
  color: #363636;
  font-family: Helvetica, Arial, sans-serif;
}

body,
button,
input,
select,
textarea {
  font-family: Helvetica, Arial, sans-serif;
}

p {
  margin-bottom: 0.8em;
  font-size: 18px;
}
p:last-child {
  margin-bottom: 0;
}

a,
a:hover,
a svg path,
button,
button:hover,
input[type=button] {
  transition: 0.3s ease all;
  text-decoration: none;
  cursor: pointer;
}

a {
  text-decoration: underline;
  transition: color 0.3s;
  font-size: 18px;
}
a:hover {
  color: red;
  opacity: 0.9;
  text-decoration: underline;
}

h1 {
  font-size: clamp(32px, 8vmin, 88px);
  font-weight: 820;
  letter-spacing: -0.02em;
  line-height: 1.05;
  text-align: left;
}

h2,
.h2 {
  font-size: clamp(28px, 5vmin, 40px);
  color: #363636;
  font-weight: bold;
  margin-bottom: 30px;
  font-weight: 820;
  letter-spacing: -0.02em;
  line-height: 1.15;
}
h2--highlighted mark.gb-highlight,
.h2--highlighted mark.gb-highlight {
  position: relative;
  line-height: 1.3;
}
h2--highlighted__primary mark.gb-highlight:after,
.h2--highlighted__primary mark.gb-highlight:after {
  background-color: #0071e3;
}
h2--highlighted__white mark.gb-highlight:after,
.h2--highlighted__white mark.gb-highlight:after {
  background-color: #fff;
}
h2--highlighted__body mark.gb-highlight:after,
.h2--highlighted__body mark.gb-highlight:after {
  background-color: #363636;
}
h2--highlighted__centered,
.h2--highlighted__centered {
  margin-bottom: 20px;
}
h2--highlighted__centered mark.gb-highlight:after,
.h2--highlighted__centered mark.gb-highlight:after {
  left: 50%;
  translate: -50%;
}

h3 {
  font-size: clamp(24px, 5vmin, 32px);
  line-height: 1.05;
  font-weight: bold;
}

h4 {
  font-size: clamp(22px, 5vmin, 28px);
  line-height: 1.05;
  font-weight: bold;
}

mark,
.gb-highlight {
  color: inherit;
}

.highlight-text {
  color: #0071e3;
}

.valign--center {
  align-items: center;
}

.offset--top__sm {
  margin-top: 50px;
}
.offset--top__md {
  margin-top: 100px;
}

#page {
  position: relative;
  overflow: unset;
  display: block;
  background: #fff;
}

#content .site-main {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
#content .site-main .inside-article {
  padding: 0;
}
#content .site-main .inside-article .entry-content > *:not(.gb-container--content__fullwidth) {
  width: 80%;
  max-width: 1600px;
  margin-inline: auto;
}
#content .site-main article {
  margin-bottom: 0;
}

.container {
  position: relative;
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  display: block;
}

.site {
  max-width: unset;
  padding: 0;
}
.site .inside-article {
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: transparent;
}

body.stop {
  overflow: hidden;
}

div {
  box-sizing: border-box;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
}

blockquote,
q {
  quotes: "" "";
}

hr {
  background-color: #eee;
  border: 0;
  height: 1px;
  margin-bottom: 20px;
}

ul,
ol {
  margin: 0;
  padding-left: 20px;
